<template>
	<div class="pos">
		<el-row>
			<el-col :span='7' class='pos-order' id='oderlist'>
				<el-tabs>
					<el-tab-pane label='点餐'>
						<el-table boder style='width:100%' :data="tableData">
							<el-table-column prop='goodsName' label='商品名称'  align="center"></el-table-column>
							<el-table-column prop='count' label='数量' width='80'  align="center"></el-table-column>
							<el-table-column prop='price' label='金额' width='90' align="center"></el-table-column>
							<el-table-column  label='操作' width='100' align="center" fixed='right'>
								<template scope="scope">
						            <el-button type="text" size="small" @click="delSingleGoods(scope.row)" >删除</el-button>
						            <el-button type="text" size="small" @click="addOrderList(scope.row)">增加</el-button>
						        </template>
							</el-table-column>
						</el-table>
						<div class="totaldiv">
							<small>数量:</small><span>{{totalCount}}</span>
							 &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
							<small>金额:</small> <span>{{totalMoney}}</span>元
						</div>
						<div class="block">
							<span class="wrapper">
							    <el-button type="warning" class='el-button'>挂单</el-button>
							    <el-button type="danger" @click='delAllGoods()'>删除</el-button>
							    <el-button type="success"@click='checkout()'>结账</el-button>
						    </span>
						</div>
					</el-tab-pane>
					<el-tab-pane label='挂单'>
						挂单
					</el-tab-pane>
					<el-tab-pane label='外卖'>
						外卖
					</el-tab-pane>
				</el-tabs>
			</el-col>
			<el-col :span='17'>
				<div>
					<div class="title">常用商品</div>
					<div class="goods-list">
						<ul>
							<li v-for="goods in ofendGoods1" @click="addOrderList(goods)">
								<span>{{goods.goodsName}}</span>
								<span class="o-price">¥{{goods.price}}元</span>
							</li>
						</ul>
					</div>
				</div>
				
				<div class="goods-type">
					<el-tabs>
						<el-tab-pane label='汉堡'>
							<div>
								<ul class="cook-list">
									 <li v-for=" item in type0Goods1 " @click="addOrderList(item)">
								        <span class="foodImg"><img :src="item.goodsImg" width="100%"></span>
								        <span class="foodName">{{item.goodsName}}</span>
								        <span class="foodPrice">¥{{item.price}}元</span>
								    </li>
								</ul>
							</div>
						</el-tab-pane>
						<el-tab-pane label='小食'>
							<ul class="cook-list">
									 <li v-for=" item in type0Goods2 "  @click="addOrderList(item)">
								        <span class="foodImg"><img :src="item.goodsImg" width="100%"></span>
								        <span class="foodName">{{item.goodsName}}</span>
								        <span class="foodPrice">¥{{item.price}}元</span>
								    </li>
								</ul>
						</el-tab-pane>
						<el-tab-pane label='饮料'>
							<ul class="cook-list">
									 <li v-for=" item in type0Goods3 " @click="addOrderList(item)">
								        <span class="foodImg"><img :src="item.goodsImg" width="100%"></span>
								        <span class="foodName">{{item.goodsName}}</span>
								        <span class="foodPrice">¥{{item.price}}元</span>
								    </li>
								</ul>
						</el-tab-pane>
						<el-tab-pane label='套餐'>
							<ul class="cook-list">
									 <li v-for=" item in type0Goods4 " @click="addOrderList(item)">
								        <span class="foodImg"><img :src="item.goodsImg" width="100%"></span>
								        <span class="foodName">{{item.goodsName}}</span>
								        <span class="foodPrice">¥{{item.price}}元</span>
								    </li>
								</ul>
						</el-tab-pane>
					</el-tabs>
				</div>
			</el-col>
		</el-row>
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
		name: 'pos',
		mounted:function(){
			var oderHeight = document.body.clientHeight;
			console.log(oderHeight)
			document.getElementById('oderlist').style.height = oderHeight + 'px';
		},
		created(){
		  var _this = this;
		  axios.post('http://jspang.com/DemoApi/oftenGoods.php')
		  .then(function(res){
//		  	console.log(_this)
//		  	console.log(res.data)
		  	_this.ofendGoods1 = res.data;
		  })
		  .catch(function(error){
		  	console.log(error)
		  });
		  
		  axios.get('http://jspang.com/DemoApi/typeGoods.php')
		  .then((res)=>{
		  	console.log(res.data)
		  	this.type0Goods1 = res.data[0];
		  	this.type0Goods2 = res.data[1];
		  	this.type0Goods3 = res.data[2];
		  	this.type0Goods4 = res.data[3];

			
			/*
			let arr = [];
			let data = res.data;
			for(let i=0; i<data.length; i++) {
				for(let j=0; j<data[i].length; j++) {
					arr.push(data[i][j])
				}
			}
			this.type0Goods1 = arr;
//			console.log(arr)
			*/
		  })
		  .catch((res)=>{
		  	console.log(res)
		  })
		  
		},
		data(){
			return {
				tableData:[
//					{
//        
//			          goodsName: '香辣鸡腿堡',
//			          price: 15,
//			          count:1
//			        }
				],
				
				ofendGoods1:[],
				
				type0Goods1:[],
				type0Goods2:[],
				type0Goods3:[],
				type0Goods4:[],
				totalCount:0,
				totalMoney:0
			}
		},
		methods:{
			addOrderList(goods){
				
				this.totalCount=0; //汇总数量清0
                this.totalMoney=0;
				//商品是否存在于订单列表中
				let isHave = false; 
				for(let i=0; i<this.tableData.length; i++) {
					 console.log(this.tableData[i].goodsId);
					if(this.tableData[i].goodsId==goods.goodsId){
						isHave = true;
					}
				}
				//根据判断的值编写业务逻辑
				if(isHave){
                //存在就进行数量添加  (o为数组的每一项，删选结果是换回符合条件的项，id是唯一的所以只有一项)
                 let arr = this.tableData.filter(o =>o.goodsId == goods.goodsId);
                 arr[0].count++;
                 //console.log(arr);
	            }else{
	                //不存在就推入数组
	                let newGoods = {
	                	goodsId:'',
	                	goodsName:'',
	                	price:'',
	                	count:''
	                }
	                newGoods={goodsId:goods.goodsId,goodsName:goods.goodsName,count:1,price:goods.price,};
	                this.tableData.push(newGoods);
	
	            }
	            
	           
	            //进行数量和价格的汇总计算 （element为数组的每一项）
	            	this.tableData.forEach((element) => {
		                this.totalCount+=element.count;
		                this.totalMoney=this.totalMoney+(element.price*element.count);   
	            	});
				},
				//删除单个商品
			delSingleGoods(goods){
				this.tableData = this.tableData.filter(j=>j.goodsId != goods.goodsId);
				this.getAllMoney();
			},
			//汇总数量金额
			getAllMoney(){
				this.totalMoney = 0;
				this.totalCount = 0;
				//判断有数据在计算
				if(this.tableData){
					this.tableData.forEach((element) => {
		              	this.totalCount+=element.count;
		                this.totalMoney=this.totalMoney+(element.price*element.count);   
	            	});
				}
			},
			//全部删除
			delAllGoods(){
				this.tableData = [];
				this.totalCount = 0;
				this.totalMoney = 0;
			},
			//模拟结账
			checkout(){
				if(this.totalCount != 0) {
					this.tableData = [];
					this.totalCount = 0;
					this.totalMoney = 0;
					this.$message({
						message:'结账成功，感谢您为本店出一份力',
						type:'success'
					})
				}else{
					this.$message({
						message:'不可以空结账',
						type:'error'
					})
				}
			}
		}
	}
</script>

<style>
	.pos-order{
		background-color: #f9fafc;
		border-right: 1px solid #1D8CE0;
		height: 100%;
	}
	.block .el-button{
		margin-top: 20px;
		font-weight: 700;
	}
	.title{
		height: 20px;
		border-bottom: 1px solid #d3dce6;
		background-color: #F9FAFC;
		padding: 10px;
		text-align: left;
	}
	.goods-list ul li{
		list-style: none;
		float: left;
		border: 1px solid #e5e9f2;
		padding: 10px;
		margin: 10px;
		background-color: #fff;
	}
	.o-price{
		color: #58b7ff;
	}
	.goods-type{
		clear: both;
	}
	.cook-list li{
		list-style: none;
		width: 22%;
		border: 1px solid #E5E9F2;
		height: auto;
		background-color: #FFFFFF;
		padding: 2px;
		overflow: hidden;
		float: left;
		margin: 5PX;
		cursor: pointer;
	}
	.cook-list span{
		display: block;
		float: left;
	}
	.foodImg {
		width: 40%;
	}
	.foodName {
		font-size: 18px;
		padding-left: 10px;
		color: brown;
	}
	.foodPrice {
		font-size: 16px;
		padding: 10px 0px 0px 16px;
	}
	.totaldiv{
		background-color: #FFFFFF;
		padding: 10px;
		border-bottom: 1px solid #E5E9F2;
		
	}
</style>


